import {Alert} from "antd"
import {useTelegram} from "../hooks/use-telegram"
import {useCallback, useEffect, useState} from "react"

export default function Restricted() {
  const {tg, user, onToggleMainButton, onClose, query_id} = useTelegram()
  const [sendInfo, setSendInfo] = useState('')

  const onSendData = useCallback(async () => {
    const data = {user, query_id}

    await fetch('https://tg-web-app-backend.onrender.com/web-data', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
  }, [user, query_id])

  useEffect(() => {
    tg.MainButton.setText('Get access')
    tg.MainButton.onClick(() => {
      setSendInfo('Access requested')
      // onSendData().then(() => {
      //   setSendInfo('Access requested')
      //   onClose()
      // })
    })
    onToggleMainButton()
  }, [tg.MainButton, onClose, onToggleMainButton]);

  return (
    <Alert
      showIcon
      type='error'
      message={sendInfo || 'Access denied'}
    />
  )
}